* {
  margin: 0;
  padding: 0; }

html, body {
  height: 100%;
  background-color: lightgray; }

.grid-container {
  width: 100%;
  max-width: 1100px;
  margin: 0 auto; }

.row:before, .row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  min-height: 1px;
  width: 100%/12;
  padding: 12px; }

.col-1 {
  width: 8.33333%; }

.col-2 {
  width: 16.66667%; }

.col-3 {
  width: 25%; }

.col-4 {
  width: 33.33333%; }

.col-5 {
  width: 41.66667%; }

.col-6 {
  width: 50%; }

.col-7 {
  width: 58.33333%; }

.col-8 {
  width: 66.66667%; }

.col-9 {
  width: 75%; }

.col-10 {
  width: 83.33333%; }

.col-11 {
  width: 91.66667%; }

.col-12 {
  width: 100%; }

.unfaithfulDogs {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: navy;
  font-size: 400%;
  padding-top: 15%; }

.sentence {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: black;
  font-size: 150%; }

#title {
  font-weight: bold;
  font-family: 'Shrikhand', cursive;
  text-align: center;
  text-shadow: 2px 1px white;
  z-index: -100;
  color: lightseagreen; }
  #title h2 {
    font-size: 450%;
    padding-top: 15%; }
  #title h6 {
    font-size: 200%; }

#startOptionsGame {
  display: flex;
  justify-content: space-between; }

#hello {
  font-size: 150%;
  font-weight: bold;
  text-shadow: 2px 1px white;
  border-bottom: 1px solid black;
  text-indent: 3%;
  padding-bottom: 2%; }

#startOptionsGame {
  padding: 15px 0 25px 0;
  border-bottom: 1px solid black; }

.btnInfo {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  background: linear-gradient(#FFF, #FF8C00); }

.btnLicence {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  background: linear-gradient(#FFF, #8A2BE2); }

.btnTutorial {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  background: linear-gradient(#FFF, #BDB76B); }

.btnNewGame {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  background: linear-gradient(#FFF, green); }

.btnInfo:hover, .btnLicence:hover, .btnTutorial:hover, .btnNewGame:hover {
  color: #8B4513; }

.btnInfo:active, .btnLicence:active, .btnTutorial:active, .btnNewGame:active {
  animation-name: changeBorder;
  animation-duration: 0.2s; }

@keyframes changeBorder {
  0% {
    border-radius: 15%; }
  100% {
    border-radius: 25px; } }

.descriptionMenu {
  text-indent: 3%;
  padding-top: 2%;
  text-shadow: 2px 1px white;
  font-size: 120%; }

#chooseBtns {
  display: flex;
  justify-content: space-around; }

#randomHero {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px #2F4F4F;
  background: linear-gradient(#8FBC8F, black);
  color: white;
  margin-top: 5%; }

.randomHeroDescription {
  z-index: 2;
  margin-left: 11%;
  position: absolute; }

.createHeroDescription {
  z-index: 2;
  margin-left: 11%;
  position: absolute; }

#createHero {
  width: 20%;
  height: 5%;
  font-weight: bold;
  font-size: 150%;
  padding: 1% 0 1% 0;
  border-radius: 15%;
  box-shadow: 0 9px #4B0082;
  background: linear-gradient(white, #4B0082);
  color: white;
  margin-top: 5%; }

#randomHero:hover, #createHero:hover {
  animation-name: biggerBtn;
  animation-duration: 1.75s; }

@keyframes biggerBtn {
  0% { }
  100% {
    width: 25%; } }

#slider {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  margin-top: -35px; }
  #slider ul {
    position: relative;
    margin: 0;
    padding: 0;
    height: 200px;
    list-style: none;
    margin-top: 20px; }
    #slider ul li {
      position: relative;
      display: block;
      float: left;
      padding: 0;
      width: 1100px;
      height: 575px; }

#sliderBtn {
  display: flex;
  justify-content: space-between; }
  #sliderBtn button.control_prev {
    width: 15%;
    height: 30px;
    background: tomato;
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    border-radius: 20px; }
  #sliderBtn button.control_next {
    width: 15%;
    height: 30px;
    background: slateblue;
    color: #fff;
    font-weight: bold;
    font-size: 20px;
    border-radius: 20px; }

.style1slider {
  text-decoration: underline;
  text-decoration-color: gold;
  font-size: 22px;
  font-weight: bold; }

#nameAccept {
  background-color: green;
  border-radius: 20px;
  margin-left: 2%;
  color: white;
  font-weight: bold;
  width: 10%;
  height: 30px; }

.nameDescription {
  margin-top: 15px;
  font-weight: bold;
  text-shadow: 2px 1px white; }

#choosenName {
  margin-top: 20px;
  font-weight: bold;
  text-shadow: 2px 1px white;
  font-size: 150%; }

#chooseRace, #chooseOccupation {
  width: 100%;
  font-size: 18px;
  color: black;
  font-weight: bold;
  padding-top: 10px;
  text-shadow: 2px 1px white; }
  #chooseRace div, #chooseOccupation div {
    width: 25%;
    float: left; }
  #chooseRace #chooseRaceDescription, #chooseRace #chooseOccupationDescription, #chooseOccupation #chooseRaceDescription, #chooseOccupation #chooseOccupationDescription {
    width: 100%; }
    #chooseRace #chooseRaceDescription p:nth-child(1), #chooseRace #chooseOccupationDescription p:nth-child(1), #chooseOccupation #chooseRaceDescription p:nth-child(1), #chooseOccupation #chooseOccupationDescription p:nth-child(1) {
      color: black;
      text-decoration: underline;
      text-decoration-color: gold;
      padding: 15px;
      text-indent: 17px; }
    #chooseRace #chooseRaceDescription p:nth-child(2), #chooseRace #chooseOccupationDescription p:nth-child(2), #chooseOccupation #chooseRaceDescription p:nth-child(2), #chooseOccupation #chooseOccupationDescription p:nth-child(2) {
      width: 95%;
      text-indent: 50px;
      margin: 0 auto; }

#randomPointsTitle {
  font-weight: bold;
  margin-top: 10px;
  margin-left: 20px; }

#features {
  display: flex;
  justify-content: space-around;
  margin-top: 30px; }
  #features button {
    width: 10%;
    height: 40px;
    font-weight: bold;
    text-shadow: 2px 1px white;
    border-radius: 25px;
    background: lightgreen; }

#show {
  font-weight: bold;
  margin-top: 25px;
  display: flex;
  justify-content: space-around; }
  #show #forcePoints, #show #strenghtPoints, #show #skillPoints, #show #intellectualsPoints, #show #charismaPoints {
    font-weight: bold; }

#featuresDescription {
  margin-top: 30px;
  font-weight: bold;
  text-shadow: 2px 1px white; }

.features2 {
  margin-top: 10px;
  font-weight: bold;
  font-size: 100%;
  text-shadow: 2px 1px white;
  text-decoration: underline;
  text-decoration-color: gold; }

#mainInfo {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }
  #mainInfo h2 {
    width: 100%;
    text-align: center; }
  #mainInfo div {
    width: 33%;
    text-align: center;
    margin-top: 10px; }
    #mainInfo div div {
      width: 100%;
      color: black;
      font-weight: bold;
      font-size: 110%;
      text-shadow: 2px 1px white; }
      #mainInfo div div p {
        width: 100%; }
      #mainInfo div div p:nth-child(2) {
        color: red;
        font-weight: bold; }
  #mainInfo .equipmentStyle p, #mainInfo .skillsStyles p {
    width: 100%;
    color: black;
    font-weight: bold;
    font-size: 110%;
    text-shadow: 2px 1px white;
    margin-top: 20px; }
  #mainInfo .equipmentStyle p:nth-child(2), #mainInfo .skillsStyles p:nth-child(2) {
    color: red;
    font-weight: bold; }

#game {
  width: 20%;
  height: 40px;
  margin: 20px 0 0 40%;
  font-weight: bold;
  color: white; }

#gameIntro {
  text-align: center;
  text-shadow: 2px 1px white;
  font-weight: bold; }
  #gameIntro p {
    padding-top: 5px;
    font-size: 125%; }

#newGame {
  background: green;
  width: 20%;
  height: 40px;
  font-weight: bold;
  color: white;
  margin-top: 25px; }

#btnsPanel {
  display: flex;
  justify-content: space-around; }
  #btnsPanel button {
    width: 15%;
    height: 35px;
    color: black;
    font-weight: bold;
    font-size: 1.25em;
    text-shadow: 2px 1px white;
    background: linear-gradient(white, darkgrey); }

#dialogPanel {
  width: 1100px;
  z-index: 20;
  background-color: lightseagreen;
  margin: 0 auto;
  margin-top: -50px;
  position: absolute; }
  #dialogPanel #close {
    width: 35px;
    height: 35px;
    color: red;
    text-align: center;
    position: absolute;
    font-size: 110%;
    margin-top: 10px; }
  #dialogPanel .styleFeatures {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: space-around;
    font-weight: bold; }

#gameDescription {
  margin-top: 30px;
  text-indent: 20px;
  font-weight: bold;
  font-size: 1.5em;
  text-shadow: 2px 1px white; }

.red {
  background-color: red; }

.green {
  background-color: green; }

.redText {
  color: red;
  font-size: 1.25em; }

#roomList, #street, #buySellItems, #buys {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 25px; }
  #roomList button, #street button, #buySellItems button, #buys button {
    width: 15%;
    height: 35px;
    font-weight: bold; }

#lookingAround {
  background: linear-gradient(white, blue);
  text-shadow: 2px 1px white; }
